/* DESKTOP */
@media screen and (min-width: 1081px) {
  h1 {
    font-size: 4vw;
  }

  .title_container {
    min-height: 15vh;
    text-align: center;
  }

  .home-button {
    font-size: 2vw;
  }

  .left-button {
    font-size: 2vw;
  }

  .corner {
    position: fixed;
    display: inline;
    top: 0vh;
    right: 0vw;
  }

  .project-corner {
    display: inline;
    z-index: 10;
  }

  .nav_container {
    /*    justify-content: center;*/
    text-align: center;
    margin-top: 2vh;
    margin-left: 25%;
    margin-right: 25%;
    margin-bottom: 2vh;
    z-index: 5;
  }

  .nav_button {
    color: var(--color4);
    background: var(--color1);
    border-width: 0;
    font-size: 2.5rem;
    padding: 0.5vh 0;
    font-family: var(--fifth-font), sans-serif;
    text-transform: uppercase;
    -webkit-transition: 0.25s ease-in-out;
    -o-transition: 0.25s ease-in-out;
    transition: 0.25s ease-in-out;
  }

  .footer {
    display: inline;
  }

  .carousel {
    width: 60%;
    height: 60%;
  }

  .project {
    margin-bottom: 4vh;
  }

  .project_image {
    width: 50%;
    margin-top: 4vmin;
    margin-bottom: 4vmin;
  }

  .project_tech {
    font-size: 1.5rem;
  }

  .project_description {
    margin: 4vmin 10vmin;
  }

  .project-array-container {
  }

  .about-headshot {
    height: 40vh;
    border-radius: 50%;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
    -webkit-transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
         -o-transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
            transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
  }

  .about-headshot:hover ~ .about-blurb {
    -webkit-transition: 0.25s ease-in-out;
    -o-transition: 0.25s ease-in-out;
    transition: 0.25s ease-in-out;
    opacity: 1;
  }

  .about-headshot-container:hover .about-headshot {
    -webkit-transition: 0.5s ease-out;
    -o-transition: 0.5s ease-out;
    transition: 0.5s ease-out;
    -webkit-transform: translateX(-20vw);
        -ms-transform: translateX(-20vw);
            transform: translateX(-20vw);
    opacity: 1;
  }

  .about-headshot-container:hover .about-blurb {
    -webkit-transition: 0.5s ease-in-out;
    -o-transition: 0.5s ease-in-out;
    transition: 0.5s ease-in-out;
    opacity: 1;
  }

  .about-blurb {
    display: block;
    -webkit-transition: 0.25s ease-in-out;
    -o-transition: 0.25s ease-in-out;
    transition: 0.25s ease-in-out;
    pointer-events: none;
    background: var(--color1);
    position: absolute;
    width: 50vw;
    height: 40vh;
    top: 57vh;
    left: 36vw;
    opacity: 0;
    font-family: var(--second-font), sans-serif;
    padding: 2vh;
    text-align: left;
  }

  .about-description {
    text-align: center;
    margin: 4vmin 4vmin;
    padding: 2vmin 2vmin;

    grid-column-start: second;
    grid-column-end: end;
    grid-row-start: first;
    grid-row-end: second;
  }

  .about-description-text {
    font-size: 2vmin;
    grid-column-start: second;
    grid-column-end: end;
    grid-row-start: first;
    grid-row-end: second;
  }

  .about-tagline-mobile {
    display: none;
  }

  .about-me {
    display: grid;
    grid-template-columns: [first] 1fr [second] 1fr [end];
    grid-template-rows: [first] auto [second] auto [third] auto [fourth] auto [fifth] auto [end];
    /* align-items: center; */
    -ms-flex-pack: center;
        justify-content: center;
  }

  .about-me-headshot-container {
    grid-column-start: first;
    grid-column-end: first;
    grid-row-start: first;
    grid-row-end: first;
    padding: 4vmin 4vmin;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: start;
        align-items: start;
    -ms-flex-pack: center;
        justify-content: center;
  }

  .about-me-headshot {
    max-height: 100%;
  }

  .about-tagline {
    display: block;
    font-size: 3em;
    margin-bottom: 4vh;
    text-align: center;
    -ms-flex-pack: center;
        justify-content: center;
    margin-left: auto;
    margin-right: auto;
    font-family: var(--fifth-font), sans-serif;
    font-weight: bold;
    line-height: 1.4;
    color: var(--color4);
  }

  .about-tagline-footer {
    display: block;
    font-size: 2em;
    margin-bottom: 4vh;
    text-align: center;
    margin-left: 4vw;
    margin-right: 4vw;
    font-family: var(--fifth-font), sans-serif;
    font-weight: bold;
    color: var(--color4);
  }

  .contact-icon:hover {
    -webkit-transform: translateY(-10%) scale(1.05);
        -ms-transform: translateY(-10%) scale(1.05);
            transform: translateY(-10%) scale(1.05);
  }

  .contact-container {
    /*text-align: left;*/
    background: var(--color1);
    margin-top: 0.5vh;
    margin-left: 20vw;
    margin-right: 20vw;
  }

  .contact-greeting-container {
    position: absolute;
    bottom: 25%;
    left: 4vw;
    z-index: 15;
    text-align: left;
    background: none;
  }

  .contact-greeting-heading {
    font-size: 4em;
    color: var(--color1);
    opacity: 0.9;
    margin-bottom: 2vh;
  }

  .contact-greeting-body {
    font-size: 2em;
    color: var(--color1);
    opacity: 0.9;
    line-height: 135%;
  }

  .contact-greeting-footer {
    font-size: 4em;
    color: var(--color1);
    opacity: 0.9;
    margin-top: 2vh;
    /*    margin-right: 4vw;*/
    text-align: center;
  }

  .contact-container {
  }

  #contact-well-input {
    font-size: 1.5em;
    margin-top: 2vh;
    margin-bottom: 1vh;
  }

  .contact-icon-copy {
    display: inline;
    margin-bottom: 1vh;
  }

  .map-container {
    display: inline-block;
  }

  .map {
    max-height: 50%;
  }

  .recomendation-container-mobile {
    display: none;
  }
}



